<template><undefined>

<h1>Popconfirm 气泡确认框</h1>
<p>点击元素，弹出气泡式的确认框。</p>
<h2 id="he-shi-shi-yong"><a class="header-anchor" href="#he-shi-shi-yong" aria-hidden="true">¶</a> 何时使用</h2>
<p>目标元素的操作需要用户进一步的确认时，在目标元素附近弹出浮层提示，询问用户。</p>
<p>和 <code v-pre="">confirm</code> 弹出的全屏居中模态对话框相比，交互形式更轻量。</p>
<h2 id="dai-ma-yan-shi"><a class="header-anchor" href="#dai-ma-yan-shi" aria-hidden="true">¶</a> 代码演示</h2>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;v-popconfirm title=\&quot;确定删除吗?\&quot; @confirm=\&quot;confirm\&quot; @cancel=\&quot;cancel\&quot;&gt;\n      &lt;a href=\&quot;javascript:;\&quot;&gt;删除&lt;/a&gt;\n  &lt;/v-popconfirm&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\n    export default{\n        methods: {\n            confirm: function() {\n                this.$message.info('点击了是');\n            },\n            cancel: function() {\n                this.$message.info('点击了否');\n            }\n        }\n    }\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
  <v-popconfirm title="确定删除吗?" @confirm="confirm" @cancel="cancel">
      <a href="javascript:;">删除</a>
  </v-popconfirm>
</template>


</div>
                  <div slot="description"><h4 id="ji-ben"><a class="header-anchor" href="#ji-ben" aria-hidden="true">¶</a> 基本</h4>
<p>最简单的用法。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">v-popconfirm</span>  <span class="hljs-attr">title</span>=<span class="hljs-string">"确定删除吗?"</span> <span class="hljs-attr">@confirm</span>=<span class="hljs-string">"confirm"</span> <span class="hljs-attr">@cancel</span>=<span class="hljs-string">"cancel"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">a</span> <span class="hljs-attr">href</span>=<span class="hljs-string">"javascript:;"</span>&gt;</span>删除<span class="hljs-tag">&lt;/<span class="hljs-name">a</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">v-popconfirm</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span>{
        <span class="hljs-attr">methods</span>: {
            <span class="hljs-attr">confirm</span>: <span class="hljs-function"><span class="hljs-keyword">function</span>(<span class="hljs-params"></span>) </span>{
                <span class="hljs-keyword">this</span>.$message.info(<span class="hljs-string">'点击了是'</span>);
            },
            <span class="hljs-attr">cancel</span>: <span class="hljs-function"><span class="hljs-keyword">function</span>(<span class="hljs-params"></span>) </span>{
                <span class="hljs-keyword">this</span>.$message.info(<span class="hljs-string">'点击了否'</span>);
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div>
