<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;v-popconfirm title=\&quot;Are you sure？\&quot; ok-text=\&quot;Yes\&quot; cancel-text=\&quot;No\&quot;&gt;\n      &lt;a href=\&quot;javascript:;\&quot;&gt;Delete&lt;/a&gt;\n  &lt;/v-popconfirm&gt;\n&lt;/template&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
