ot;确定删除吗?\&quot; placement=\&quot;bottomLeft\&quot;&gt;\n                &lt;v-button&gt;下左&lt;/v-button&gt;\n            &lt;/v-popconfirm&gt;\n            &lt;v-popconfirm title=\&quot;确定删除吗?\&quot; placement=\&quot;bottom\&quot;&gt;\n                &lt;v-button&gt;下边&lt;/v-button&gt;\n            &lt;/v-popconfirm&gt;\n            &lt;v-popconfirm title=\&quot;确定删除吗?\&quot; placement=\&quot;bottomRight\&quot;&gt;\n                &lt;v-button&gt;下右&lt;/v-button&gt;\n            &lt;/v-popconfirm&gt;\n        &lt;/div&gt;\n    &lt;/div&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:null,&quot;style&quot;:&quot;\n    .top {\n        margin-left: 125px;\n    }\n    .left {\n        float: left;\n        width: 60px;\n    }\n    .right {\n        width: 60px; \n        margin-left: 380px;\n    }\n    .bottom {\n        clear: both;\n        margin-left: 125px;\n    }\n    .ant-tooltip{\n        margin:5px;\n        position:relative;\n        display:inline-block;\n    }\n&quot;}">
