<h2 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h2>
<h3 id="popconfirm-props"><a class="header-anchor" href="#popconfirm-props" aria-hidden="true">¶</a> Popconfirm Props</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>title</td>
<td>确认框的描述</td>
<td>string</td>
<td>-</td>
</tr>
<tr>
<td>okText</td>
<td>确认按钮文字</td>
<td>string</td>
<td><code v-pre="">确定</code></td>
</tr>
<tr>
<td>cancelText</td>
<td>取消按钮文字</td>
<td>string</td>
<td><code v-pre="">取消</code></td>
</tr>
<tr>
<td>placement</td>
<td>气泡框位置，可选 <code v-pre="">top</code> <code v-pre="">left</code> <code v-pre="">right</code> <code v-pre="">bottom</code> <code v-pre="">topLeft</code> <code v-pre="">topRight</code> <code v-pre="">bottomLeft</code> <code v-pre="">bottomRight</code> <code v-pre="">leftTop</code> <code v-pre="">leftBottom</code> <code v-pre="">rightTop</code> <code v-pre="">rightBottom</code></td>
<td>string</td>
<td><code v-pre="">top</code></td>
</tr>
</tbody>
</table>
<h3 id="popconfirm-events"><a class="header-anchor" href="#popconfirm-events" aria-hidden="true">¶</a> Popconfirm Events</h3>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>confirm</td>
<td>点击确认时触发的事件</td>
<td>-</td>
</tr>
<tr>
<td>cancel</td>
<td>点击取消时触发的事件</td>
<td>-</td>
</tr>
</tbody>
</table>
<h2 id="zhu-yi"><a class="header-anchor" href="#zhu-yi" aria-hidden="true">¶</a> 注意</h2>
<p>请确保 <code v-pre="">Popconfirm</code> 的子元素能接受 <code v-pre="">onMouseEnter</code>、<code v-pre="">onMouseLeave</code>、<code v-pre="">onFocus</code>、<code v-pre="">onClick</code> 事件。</p>
</undefined></template>
<style scoped>
    .top {
        margin-left: 125px;
    }
    .left {
        float: left;
        width: 60px;
    }
    .right {
        width: 60px; 
        margin-left: 380px;
    }
    .bottom {
        clear: both;
        margin-left: 125px;
    }
    .ant-tooltip{
        margin:5px;
        position:relative;
        display:inline-block;
    }
</style>
<script>
    export default {
        methods: {
            confirm: function() {
                this.$message.info('点击了是');
            },
            cancel: function() {
                this.$message.info('点击了否');
            }
        }
    }
</script>


